<template>
  <div class="user-manage container">
    <Alert v-if="alert" v-bind:message="alert"></Alert>
    <h1 class="page-header">用户管理系统</h1>

    <input type="text" class="form-control" placeholder="搜索" v-model="filterInput">
    <br>
    <table class="table table-striped">
      <thead>
        <tr>
          <th>姓名</th>
          <th>电话</th>
          <th>邮箱</th>
          <th></th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="manage in filterBy(manages, filterInput)" :key="manage.name">
          <td>{{manage.name}}</td>
          <td>{{manage.phone}}</td>
          <td>{{manage.email}}</td>
          <td><router-link class="btn btn-default" :to="'manage/' + manage.id">详情</router-link></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import Alert from './Alert'

export default {
  name: 'user-manage',
  data () {
    return {
      manages:[
        {
      "name": "Henry",
      "phone": "333-44-555",
      "email": "henry@gmail.com",
      "id": 1,
      "age": 30,
      "companyId": 1
    },
    {
      "name": "Bucky",
      "phone": "333-44-555",
      "email": "bucky@gmail.com",
      "id": 2,
      "age": 30,
      "companyId": 2
    },
    {
      "name": "Emily",
      "phone": "333-44-555",
      "email": "emily@gmail.com",
      "id": 3,
      "age": 30,
      "companyId": 3
    },
    {
      "name": "Elyse",
      "phone": "333-44-555",
      "email": "elyse@gmail.com",
      "id": 4,
      "age": 30,
      "companyId": 3
    },
    {
      "name": "55555",
      "phone": "bbb",
      "email": "a",
      "education": "a",
      "graduationschool": "a",
      "profession": "a",
      "profile": "a",
      "id": 5
    }
      ],
      alert: "",
      filterInput:""
    }
  },
  methods:{
    fetchManage(){
      this.$http.get('http://localhost:3000/users')
        .then((response) => {
          // console.log(response);
          // this.manages = response.body;
          this.manages = response.data;
        })
    },
    filterBy(manages, value) {
      return manages.filter((manage) => {
        return manage.name.match(value);
      })
    }
  },
  created(){
    if(this.$route.query.alert) {
      this.alert = this.$route.query.alert;
    }
    this.fetchManage();
  },
  updated(){
    this.fetchManage();
  },
  components:{
    Alert
  }
}
</script>

<style scoped>

</style>



