<template>
  <div class="details container">
    <router-link to="/" class="btn btn-default">返回</router-link>
    <h1 class="page-header">
        {{manage.name}}

        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/' + manage.id">编辑</router-link>
            <button class="btn btn-danger" @click='deleteManage(manage.id)'>删除</button>
        </span>
    </h1>
    <ul class="list-group">
        <li class="list-group-item">
            <span class="glyphicon glyphicon-earphone">
                {{manage.phone}}
            </span>
        </li>
        <li class="list-group-item">
            <span class="glyphicon glyphicon-envelope">
                {{manage.email}}
            </span>
        </li>
    </ul>

    <ul class="list-group">
        <li class="list-group-item">
            <span class="glyphicon glyphicon-education">
                {{manage.education}}
            </span>
        </li>
        <li class="list-group-item">
            <span class="glyphicon glyphicon-asterisk">
                {{manage.graduationschool}}
            </span>
        </li>
        <li class="list-group-item">
            <span class="glyphicon glyphicon-font">
                {{manage.profession}}
            </span>
        </li>
        <li class="list-group-item">
            <span class="glyphicon glyphicon-user">
                {{manage.profile}}
            </span>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'managedetails',
  data () {
    return {
      manage:""
    }
  },
  methods:{
    fetchManage(id){
      this.$http.get('http://localhost:3000/users/' + id)
        .then((response) =>{
        //   console.log(response);
        //   this.manage = response.body;
        this.manage = response.data;

        })
    },
    deleteManage(id){
        // console.log(id);
        this.$http.delete('http://localhost:3000/users/' + id)
            .then((response) => {
                this.$router.push({path: '/', query:{alert:'用户删除成功！'}});
            })
    }
  },
  created(){
    this.fetchManage(this.$route.params.id);
  },
}
</script>

<style scoped>

</style>



